/* src/components/Header.css */

.header-container {
    color:#5f12e6; /* Text color */
    padding: 30px 0; /* Add padding top and bottom */
    background-image: url('./bg2.jpg'); /* Specify the path to your background image */
    background-size: cover; /* Cover the entire header */
    background-position: center; /* Center the background image */
    margin-bottom: -20px; /* Remove the bottom margin */
}

/* Remaining styles... */

.blue-text {
    color: blue;
  }
.blue-background {
    background-color: blue;
    color: white;
    padding: 10px;
  }
  
.header-background {
    background-image: url('./bg2.jpg'); /* Specify the path to your image file */
}

.header-content {
    max-width: 1200px; /* Limit the width of the content */
    margin: 0 auto; /* Center the content horizontally */
}
/* Home.css */

.home-section {
    display: flex;
  }
  
  .left-content {
    flex: 1;
    padding-right: 20px;
  }
  
  .right-content {
    flex: 1;
  }
  
  /* Add other styles as needed */
  
.header-content h1 {
    margin: 0; /* Remove default margin of h1 */
}

nav ul {
    list-style-type: none; /* Remove default bullet points */
    padding: 0; /* Remove default padding */
}

nav ul li {
    display: inline; /* Display list items horizontally */
    margin-right: 20px; /* Add some spacing between list items */
}

nav ul li a {
    color: #ffff; /* Text color of links */
    text-decoration: none; /* Remove underline from links */
}

/* Adding orange line under links when hovered */
nav ul li a:hover {
    position: relative; /* Position relative for pseudo-element */
}

nav ul li a:hover::after {
    content: ''; /* Add content for pseudo-element */
    position: absolute; /* Position absolute for pseudo-element */
    left: 0; /* Align left */
    bottom: -3px; /* Move below the link */
    width: 100%; /* Set width to cover entire link */
    height: 2px; /* Set height for line */
    background-color: orange; /* Color of the line */
}

/* Additional styles for the table and buttons */

table {
    margin-left: 50px; /* Position the table to the left side */
}

button {
    border-radius: 10px; /* Apply border radius to buttons */
    padding: 10px 20px; /* Add padding to buttons */
    margin-bottom: 10px; /* Add space between rows */
    display: block; /* Display buttons as block elements */
    transition: background-color 0.3s; /* Smooth transition effect */
}

button:hover {
    background-color: orange; /* Change button color to orange on hover */
}
